* {
	font-family: 'Roboto', sans-serif; }
header {
	position: relative;
	background-color: #212121; }
nav {
	position: fixed;
	width: 100%;
	z-index: 2;
	height: 50px;
	background-color: #212121; }
.menu {
	display: flex;
	justify-content: space-between;
	margin: 0;
	padding: 13px 70px;
	list-style-type: none;
	.menu_link {
		color: #FFF;
		font-size: 15px;
		font-weight: 300;
		text-decoration: none; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 48px; } }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 87px;
	background-color: rgba(0, 0, 0, .6);
	.subheader_logo {
		display: block;
		margin-top: 16px;
		max-width: 100%; }
	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #FFF;
		font-size: 13px;
		font-weight: 400; }
	.subheader_call {
		color: #FFF;
		text-align: right;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px; }
	.subheader_phone {
		display: block;
		margin-top: 8px;
		text-decoration: none;
		color: #FFF;
		text-align: right;
		font-size: 22px;
		font-weight: 400;
		line-height: 12px;
		text-transform: uppercase; }
	.subheader_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset;
		border: none;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 14px;
		font-weight: 400;
		line-height: 46px;
		text-transform: uppercase; } }
.promo {
	min-height: 800px;
	background: url(../img/uber_bg.png) center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		color: #FFF;
		text-align: center;
		font-size: 60px;
		font-weight: 700;
		line-height: 60px;
		text-transform: uppercase;
		margin: 0; }
	.promo_subheader {
		margin: 0;
		margin-top: 22px;
		color: #FFF;
		text-align: center;
		font-size: 36px;
		font-weight: 700;
		line-height: 36px;
		text-transform: uppercase; }
	.promo_descr {
		margin-top: 23px;
		color: #FFF;
		text-align: center;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px; }
	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		border: none;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 18px;
		font-weight: 400;
		line-height: 63px;
		text-transform: uppercase; } }
.reasons {
	padding: 71px 0 65px 0;
	.reasons_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		.reasons_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: #1eacc7;
			border-radius: 100%; }
		.reasons_descr {
			width: 343px;
			.reasons_subtitle {
				line-height: 21px;
				color: #1eacc7;
				font-size: 17px;
				font-weight: 400; }
			.reasons_text {
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px; } } } }
.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }
.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }
.subtitle {
	color: #222;
	font-size: 17px;
	font-weight: 700;
	margin-top: 19px; }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0; }
